import {
  CHAT_DATASET_PICKER_ICON_ID,
  CHAT_WITH_FILES_ICON_ID,
  CHAT_WITH_WATCHLIST_ICON_ID,
} from "@rp/common/settings"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { ActionKey, WatchlistsOutlined } from "@rp/react/web/icons"
import { Button, Fade, Grow, IconButton } from "@rp/react/web/mui/core"
import { ArrowForward, FolderOpenOutlined, StopCircleOutlined } from "@rp/react/web/mui/icons"

function NewPromptIcon() {
  return <ArrowForward />
}

function AddPromptIcon() {
  return <ArrowForward sx={{ transform: "rotateZ(-90deg)" }} />
}

function SubmitPromptButton({
  onClick,
  disabled = false,
  variant,
}: {
  onClick: () => void
  disabled: boolean
  variant: "new" | "add"
}) {
  const { isMobile } = useDeviceInfo()
  const isNewChat = variant === "new"

  return (
    <Grow in>
      <Button
        variant="contained"
        sx={{
          borderRadius: "8px",
          position: "absolute",
          right: 12,
          bottom: isMobile ? 8 : 12,
          height: 32,
          width: 32,
          minWidth: 0,
        }}
        onClick={onClick}
        disabled={disabled}
        aria-label="Send prompt"
      >
        {isNewChat ? <NewPromptIcon /> : <AddPromptIcon />}
      </Button>
    </Grow>
  )
}

function GeneratingIcon({
  onClick,
  disabled = false,
}: {
  onClick: () => void
  disabled?: boolean
}) {
  const { isMobile } = useDeviceInfo()

  return (
    <Fade in>
      <IconButton
        aria-label="stop generating"
        sx={{
          position: "absolute",
          right: 16,
          bottom: isMobile ? 8 : 12,
          height: 32,
          width: 32,
          minWidth: 0,
        }}
        onClick={onClick}
        disabled={disabled}
      >
        <StopCircleOutlined />
      </IconButton>
    </Fade>
  )
}

function AttachFileButton({
  onClick,
  disabled = false,
  tooltipDisabled = false,
}: {
  onClick: () => void
  disabled?: boolean
  tooltipDisabled?: boolean
}) {
  return (
    <RPTooltip title="Select a File" placement="top" disableHoverListener={tooltipDisabled}>
      <span>
        <IconButton
          id={CHAT_WITH_FILES_ICON_ID}
          sx={{
            height: 32,
            width: 32,
            minWidth: 0,
            marginRight: "4px",

            "& svg": { color: "grey.300", fontSize: "20px" },
          }}
          disabled={disabled}
          onClick={onClick}
        >
          <FolderOpenOutlined />
        </IconButton>
      </span>
    </RPTooltip>
  )
}

function AddWatchlistButton({
  onClick,
  disabled = false,
  tooltipDisabled = false,
}: {
  onClick: () => void
  disabled?: boolean
  tooltipDisabled?: boolean
}) {
  return (
    <RPTooltip title="Select a Watchlist" placement="top" disableHoverListener={tooltipDisabled}>
      <span>
        <IconButton
          id={CHAT_WITH_WATCHLIST_ICON_ID}
          disabled={disabled}
          onClick={onClick}
          size="small"
          sx={{
            backgroundColor: disabled ? "overlay.surface" : undefined,
            height: 32,
            width: 32,
            marginRight: "4px",

            "& svg": {
              color: "grey.300",
            },
          }}
        >
          <WatchlistsOutlined />
        </IconButton>
      </span>
    </RPTooltip>
  )
}

function AddSourceButton({
  onClick,
  active = false,
  disabled = false,
  tooltipDisabled = false,
}: {
  onClick: () => void
  active?: boolean
  disabled?: boolean
  tooltipDisabled?: boolean
}) {
  return (
    <RPTooltip
      title="Select a Source"
      placement="top"
      PopperProps={{ sx: { display: tooltipDisabled ? "none" : undefined } }}
    >
      <span>
        <IconButton
          id={CHAT_DATASET_PICKER_ICON_ID}
          aria-label="Select a Source"
          disabled={disabled}
          onClick={onClick}
          size="small"
          sx={{
            backgroundColor: active ? "overlay.surface" : undefined,
            height: 32,
            width: 32,
            marginRight: "4px",

            "& svg": {
              color: "grey.300",
            },
          }}
        >
          <ActionKey width={20} height={20} />
        </IconButton>
      </span>
    </RPTooltip>
  )
}

export {
  AddPromptIcon,
  AddSourceButton,
  AttachFileButton,
  GeneratingIcon,
  SubmitPromptButton,
  AddWatchlistButton,
}
